body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
*{
  margin: 0;
  padding: 0;
  list-style-type: none;
  -webkit-tap-highlight-color: transparent;
}
@font-face {
  font-family: 'iconfont';  /* project id 961479 */
  src: url('https://at.alicdn.com/t/font_961479_57y7r0b1elp.eot');
  src: url('https://at.alicdn.com/t/font_961479_57y7r0b1elp.eot?#iefix') format('embedded-opentype'),
  url('https://at.alicdn.com/t/font_961479_57y7r0b1elp.woff') format('woff'),
  url('https://at.alicdn.com/t/font_961479_57y7r0b1elp.ttf') format('truetype'),
  url('https://at.alicdn.com/t/font_961479_57y7r0b1elp.svg#iconfont') format('svg');
}

.App{
  width: 100%;
  height: 100vh;
  background-color: white;
}
.topNav {
  display: flex;
  flex-direction: row; }
  .topNav > li {
    height: 40px;
    flex: 1 1;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center; }
    .topNav > li.active {
      color: #e54847; }

.border-bottom {
  height: 2px;
  background-color: lightgray;
  width: 100%; }
  .border-bottom .border-bottom-bar {
    width: 50%;
    height: 2px;
    background-color: #e54847;
    -webkit-transition: all 0.2s linear;
    transition: all 0.2s linear;
    -webkit-transform: translateX(0);
            transform: translateX(0); }
    .border-bottom .border-bottom-bar.active {
      -webkit-transform: translateX(100%);
              transform: translateX(100%); }

.movie {
  display: flex;
  flex-direction: column;
  height: 100%; }

.swiperContent {
  flex: 1 1;
  overflow: auto; }

.Page {
  width: 100%;
  height: 100%;
  background-color: white;
  display: flex;
  flex-direction: column; }

.title {
  width: 100%;
  height: 40px;
  background-color: #e54847;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  color: white;
  position: relative; }
  .title .back {
    font-size: 24px;
    position: absolute;
    left: 10px;
    color: white;
    font-family: "iconfont"; }

.m-item {
  display: flex;
  flex-direction: row;
  box-sizing: border-box;
  padding: 2px;
  border-bottom: 1px solid #ececec;
  margin: 2px 0; }
  .m-item .left {
    width: 80px; }
    .m-item .left img {
      width: 100%;
      height: auto;
      display: block; }
  .m-item .center {
    flex: 1 1;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    font-size: 14px;
    margin: 0px 2px; }
  .m-item .right {
    width: 60px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center; }
    .m-item .right .btnBuy {
      width: 50px;
      height: 30px;
      border-radius: 5px;
      background-color: #e54847;
      color: white;
      border: none; }

.comingTitle {
  padding-left: 2px;
  font-size: 18px;
  color: gray;
  margin: 5px 0;
  box-sizing: border-box; }

.m-item {
  display: flex;
  flex-direction: row;
  box-sizing: border-box;
  padding: 2px;
  border-bottom: 1px solid #ececec;
  margin: 2px 0; }
  .m-item .left {
    width: 80px; }
    .m-item .left img {
      width: 100%;
      height: auto;
      display: block; }
  .m-item .center {
    flex: 1 1;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    font-size: 14px;
    margin: 0px 2px; }
  .m-item .right {
    width: 60px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center; }
    .m-item .right .btnBuy, .m-item .right .btnPre {
      width: 50px;
      height: 30px;
      border-radius: 5px;
      background-color: #e54847;
