.Index {
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
  background-color: white; }
  .Index .routerViewContent {
    flex: 1 1;
    overflow: auto; }
  .Index .tabBar {
    border-top: 1px solid lightgray;
    display: flex;
    flex-direction: row; }
    .Index .tabBar > .link {
      display: block;
      color: black;
      text-decoration: none;
      height: 55px;
      flex: 1 1;
      display: flex;
      flex-direction: column;
      justify-content: space-around;
      align-items: center; }
      .Index .tabBar > .link .icon {
        font-family: "iconfont";
        font-size: 34px;
        font-weight: bold; }
      .Index .tabBar > .link .text {
        font-size: 14px; }
      .Index .tabBar > .link.active {
        color: #e54847; }


